import $ from 'jquery'
const baseUrl = 'http://localhost:8080/Library_war_exploded'

const getBooks = () => {
    return $.get(`${baseUrl}/BookServlet`, {
        flag: 'getAllBook'
    }, response => {
        return response
    })
}

const getTypes = () => {
    return $.get(`${baseUrl}/BookServlet`, {
        flag: 'getBookType'
    }, response => {
        return response
    })
}

const reserve = (props) => {
    return $.post(`${baseUrl}/UserServlet`, {
        bookIsbn: props,
        flag:'setBooking'
    }, response => {
        return response
    })
}

export default { getBooks, getTypes,reserve }